@import "../common/styles/sass/mixins";

.contactsBlock {
  @include block($bgSecondColor);

  .contactsContainer {
    @include container;

    .main_title {
      @include mainTitle;

      .title {
        @include titleFront;

        & h3 {
          @include titleH3;

          .bg {
            @include titleBG;
          }
        }
      }
    }

    .contacts {
      border: 1px solid;
      background-color: #111;
      width: 100%;
      display: flex;
      justify-content: center;

      .form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 80%;
        @media screen and (max-width: 300px) {
          width: 100%;
        }
      }
    }
  }
}




